import Vue from 'vue'
import Vuex from 'vuex'
import decode from 'jwt-decode'
import router from '../router/index'
import axios from 'axios'
Vue.use(Vuex)

const store= new Vuex.Store({
  state: {
    token:'',
    usuario:''
  },
  mutations: {
    obtenerUsuario(state, payload) {
      state.token = payload;
      if (payload === '') {
        state.usuario = ''
        router.push({ name: 'Login' });
      } else {
        state.usuario = decode(payload)
        var privilegios=state.usuario.data.rol;
        // console.log(privilegios);
        if(privilegios==='sinRol')
            {router.push({ name:'Espera'})}
        else if(privilegios==='Administrador')
            {router.push({ name:'Administrar' })}
        else if(privilegios==='Imagen')
            {router.push({ name:'Imagenes'})}
        else if(privilegios==='Laboratorio Pams')
            {router.push({ name:'LabClinico'})}
        else if(privilegios==='Patologia')
            {router.push({ name: 'Patologia'})}
        else if(privilegios==='Admision')
            {router.push({ name:'Trazabilidad'})}
        else if(privilegios==='Laboratorio Chincha')
            {router.push({ name:'DiagChincha' })}
        else if(privilegios==='Laboratorio Lima')
            {router.push({ name: 'DiagLima' })}            
              }                
        },

  },
  actions: {
     async getPaciente({commit},payload){
      
      const api =localStorage.getItem('api');
      var headers={"Content-Type": "application/json"}
      if(api==1){       
        headers.Authorization= "Bearer e65b0c6e585fbb6a8daa13e3b1921199fc0022412282c45e9d161b85b46dda0d";
      }else if(api==2){
        headers.Authorization= "Bearer 611d7a8ca3025370a474a777bfb5f5bf17b1d65cc6592b4247b70bfbab87a499";
        }  
      try {
            const {data} =  await axios.get(`https://apiperu.dev/api/dni/${payload.payload}`,{headers})
            if(data.data)
              {
              var paciente={
                      nombres:data.data.nombres,
                      apellidos:data.data.apellido_paterno+' '+data.data.apellido_materno
                    };
                    
              return paciente;
              }          
                  console.log('dentro'+api);
                  return  data.message;
               } catch (error) {  console.log(error) }   

      },
      guardarUsuario({ commit }, payload) {
      localStorage.setItem('token', payload);
      //este api es para usar el primer token 
      localStorage.setItem('api', 1);
      //Esta commit es usado para no actualizar 
      //pero al reenviar a su pagina se nota la cabecera antes que desaparezca el login 
      // commit('obtenerUsuario', payload);
        location.reload();
    },
    leerToken({commit}){
      const token= localStorage.getItem('token');
      if(token){
        commit('obtenerUsuario',token)
      }else{
        commit('obtenerUsuario','')
      }
      
    },
    cerrarSession(){
      localStorage.removeItem('token');
      //al recargar la apgina automaticamente se borra el historial de rutas y 
      //ya no pueden regresar a la ruta anterior con 
      //las flechas del navegador :V 
      location.reload();
      
      
    },
 
    
  },
  modules: {
  },
  getters:{
    estadoActivo: state => !!state.token,

    
  }
})
export default store;