<template>
<div>
    <v-app-bar dense app dark :color="color_salud">
      <v-app-bar-nav-icon @click="drawer = !drawer" ></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
       <v-toolbar-title  class="hidden-xs-and-down"> {{name}} </v-toolbar-title>
      <v-btn icon>
        <v-icon>mdi-account-circle </v-icon>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer 
    :disable-route-watcher="true"
    style="background-color:#1565C0;"
    v-model="drawer"  
    app
    dark
    >
      <v-list-item >
        <v-list-item-content>
          <v-list-item-title :color="color_salud" class="text-lg-h6 layout align-center">
             <v-avatar class="mr-2">
              <img
                src="../assets/logo_white.png"
                height="50px"
                alt="John"
              >
            </v-avatar>
            PAMS Exams
          </v-list-item-title>
        </v-list-item-content>
    
      </v-list-item>
      <v-divider>  </v-divider>
        <!-- la prop "dense" es para que sea mas pequeñop las letras del menu-->
        <!-- la prop "nav" es para dar un poco de espaciado entre los textos del menu-->
          <!-- envolver con v-list-item-group a los v-list-item nos puede permitir manejar que cada click puede guardar el navigation drawer -->
      <v-list
      dense
      nav
      shaped
      class="pams_list"
      >
      <v-list-item-group
        v-model="group"
  
        >
      <!-- replace es para que no se guarde nada en el historial -->
           <v-list-item  v-for="(item,index) in imprimirListaNavegacion()" :key="index" link :to="item.route" >
              <v-list-item-icon>
                <v-icon>
                  {{item.icon }}
                </v-icon>
            </v-list-item-icon>
                <v-list-item-content >
                  <v-list-item-title >
                       {{item.name}}
                  </v-list-item-title>
                </v-list-item-content>
            </v-list-item>
          <v-list-item  @click="cerrarSession" >
              <v-list-item-icon>
                <v-icon>
                  person_remove
                </v-icon>
            </v-list-item-icon>
                <v-list-item-content >
                  <v-list-item-title >
                      Salir
                  </v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list-item-group>

      </v-list>
     
    </v-navigation-drawer>

</div>
</template>
<script>
import {mapActions,mapState} from 'vuex'
export default {
name:'navDrawer',
props:{
  name:String
},
data() {
 
    return {
        color_salud:'blue darken-3',
        drawer:false,
        group:null,
        AdministradorNav: [
        {name:'Administrador', icon:'mdi-account-multiple-outline',route:'/administrar'},
        {name:'Trazabilidad', icon:'settings_input_antenna',route:'/trazabilidad'},
        {name:'Laboratorio Clínico', icon:'mdi-sword',route:'/labClinico'},
        {name: 'LabClinicoPendiente',icon:'mdi-sword',route: '/labClinicoPendiente'},
        {name:'Patologia', icon:'biotech',route:'/patologia'},
        {name: 'PatologiaPendiente',icon:'biotech', route: '/patologiaPendiente'},
        {name:'Imágenes', icon:'mdi-account-multiple-outline',route:'/imagenes'},
        {name:'Chincha',icon: 'mdi-account-multiple-outline',route:'/labTercerizado_Chincha'},
        {name:'Lima', icon:'mdi-account-multiple-outline',route:'/labTercerizado_Lima'},
        

      ], 
      AdmisionNav: [
        {name:'Trazabilidad', icon:'settings_input_antenna',route:'/trazabilidad'},
      ], 
        LaboratorioNav:[
          {name:'Laboratorio Clínico', icon:'biotech',route:'/labClinico'},
           {name: 'LabClinicoPendiente',icon:'mdi-sword',route: '/labClinicoPendiente'}
        ],
        PatologiaNav:[
           {name:'Patologia', icon:'people',route:'/patologia'},
           { name: 'PatologiaPendiente',icon:'people', route: '/patologiaPendiente'}
        ],
        ImagenesNav:[
          {name:'Imágenes', icon:'mdi-account-multiple-outline',route:'/imagenes'},
        ],
        LabChinchaNav:[
          {name:'Chincha',icon: 'mdi-account-multiple-outline',route:'/labTercerizado_Chincha'},
        ],
        LabLimaNav:[
         {name:'Lima', icon:'mdi-account-multiple-outline',route:'/labTercerizado_Lima'},
          ]
        } 
},
computed:{
...mapState(['usuario'])
},
   watch:{
    group(){
      this.drawer= !this.drawer;
    }
   },
   methods: {
     ...mapActions(['cerrarSession']),
     imprimirListaNavegacion(){
       if(this.usuario){
        var rol= this.usuario.data.rol;
         if(rol==='Administrador'){
            return this.AdministradorNav;}
        else if(rol==='Imagen'){
           return this.ImagenesNav;}
        else if(rol==='Laboratorio Pams'){
            return this.LaboratorioNav;}
        else if(rol==='Patologia'){
           return this.PatologiaNav;}
        else if(rol==='Admision'){
          return this.AdmisionNav;}
           
           }             


     }
    
   },
}
</script>
<style scope>

.pams_list .v-list-item-group .v-list-item--active{
  background-color: #FFFFFF;
  color: #1565C0  ;
}
  
</style>