<template>
  <v-responsive height="100vh" :class="color_salud" class=" align-center">
    <v-row class="justify-center">
      <v-col xs="6" sm="6" md="5" lg="4">
        <v-card class="pa-3 mx-2 pa-lg-5">
          <h2 :class="color_text_salud" class="text-center">LOGIN</h2>
          <v-form @submit.prevent="validate" ref="formulario" lazy-validation>
            <v-text-field
              v-model="email"
              :rules="emailRules"
              :color="color_salud"
              class="my-1"
              label="E-mail"
              hide-details="auto"
              required
            >
            </v-text-field>
            <v-text-field
              :color="color_salud"
              v-model="password"
              class="my-1"
              :rules="passwordRules"
              :type="showPassword ? 'text' : 'password'"
              :append-icon="showPassword ? 'visibility' : 'visibility_off'"
              label="Password"
              @click:append="showPassword = !showPassword"
            >
            </v-text-field>
            <v-btn class="my-5" block dark  :color="color_salud" type="submit">
              Ingresar
            </v-btn>
          </v-form>       
            <Registrar @mensajereg="ShowMessageRegister"></Registrar>
          <v-alert v-if="registroHecho" dense class="text-center" type="success">
          Registro Exitoso</v-alert
        >
        </v-card>
       
      </v-col>
    </v-row>
    <div class="ma-2">
      <v-snackbar
        width="9"
        max-width="9"
        class="mx-0"
        :timeout="2000"
        v-model="errorCredenciales"
        :vertical="true"
        color="blue lighten-4"
        :class="color_text_salud"
      >
        <h3>{{dataError}}</h3>

        <template v-slot:action="{ attrs }">
          <v-btn
            color="blue accent-4"
            dark
            v-bind="attrs"
            @click="errorCredenciales = false"
          >
            Cerrar
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-responsive>
</template>

<script>
// @ is an alias to /src
// import {required,email} from 'vuelidate/lib/validators'
import {mapActions,mapState} from 'vuex'
import Registrar from'./components/Registro'
export default {
  name: "Home",
  components: {Registrar},
 
  computed:{
      ...mapState(['token'])
  },
  data() {
    return {
      color_salud:'blue darken-3',
      color_text_salud:'blue--text text-darken-3',
       registroHecho: false,
      errorCredenciales: false,
      dataError:'',
      showPassword: false,
      email: "",
      password: "",
      emailRules: [
        (v) => !!v || "E-mail es requerido",
        (v) => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || "e-mail invalid."],
      passwordRules: [(v) => !!v || "Se requiere Password"],
    };
  },
  methods: {
    //   Hora(d){
    //  return d.getUTCFullYear() + "-" + (1 + d.getUTCMonth()) + "-" + d.getUTCDate() + " " + d.getUTCHours() + ":" + d.getUTCMinutes() + ":" + d.getUTCSeconds();
    //   }
    ...mapActions(['guardarUsuario']),
    validate() {
      var ingreso = this.$refs.formulario.validate();
      if (ingreso) {    
       this.ingresar(this.email,this.password)
      }
    },
      ingresar(e,p) {
        this.axios.post('/Login',{email:e,pass:p})
        .then(res=>{  
          
        this.guardarUsuario(res.data.token);
        // this.email='';
        // this.password='';
        // this.dataError='';
        
        })
        .catch(err=>{
           this.email='';
           this.password='';      
           this.dataError=err.response.data.mensaje;           
           this.errorCredenciales = true;   
        })
         

    },
    ShowMessageRegister(){
      this.registroHecho=true;
      setTimeout(()=>{this.registroHecho=false}, 3000);
    }
  },
};
</script>
